play: inline-block;\n}\n[type=button],\n[type=reset],\n[type=submit],\nbutton {\n    -webkit-appearance: button;\n}\nbutton, select {\n    text-transform: none\n}\n\na {\n    background:transparent;\n    color: $link-color;\n    text-decoration: none;\n    &:hover {\n        text-decoration: underline;\n        color: $link-hover-color;\n    }\n}\n\nbutton, [type=\"button\"], [type=\"submit\"] {\n    cursor: pointer;\n}\n\n.small, small {\n    font-size: 80%;\n}\n\ninput[type=search] {\n    appearance: none;\n}\n\nh1 {\n    font-size: 2em;\n}\nh2 {\n    font-size: 1.5em;\n}\nh3 {\n    font-size: 1.25em;\n}\nh4 {\n    font-size: 1em;\n}\nh5 {\n    font-size: .9em;\n}\nh6 {\n    font-size: .8em;\n}\n\n#app, #wrapper {\n    min-height: 100vh;\n}\n\n.ratio-item {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    overflow: hidden;\n}\n\n.ratio-21by9 {\n    width: 100%;\n    padding-bottom: 42.857143%;\n    height: 0;\n    position: relative;\n}\n.ratio-16by9 {\n    width: 100%;\n    padding-bottom: 56.25%;\n    height: 0;\n    position: relative;\n}\n.ratio-16by10 {\n    width: 100%;\n    padding-bottom: 62.5%;\n    height: 0;\n    position: relative;\n}\n.ratio-5by4 {\n    width: 100%;\n    padding-bottom: 80%;\n    height: 0;\n    position: relative;\n}\n.ratio-4by3 {\n    width: 100%;\n    padding-bottom: 75%;\n    height: 0;\n    position: relative;\n}\n.ratio-1by1 {\n    width: 100%;\n    padding-bottom: 100%;\n    height: 0;\n    position: relative;\n}\n\n.ratio-link {\n    display: block;\n    height: 100%;\n    width: 100%;\n    position: relative;\n    overflow: hidden;\n}\n\n.ratio-img {\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    width: 100%;\n    margin: auto;\n    height: auto; \n}\n\n\n\n.ratio-media {\n    display: block;\n    width: 100%;\n    height: 100%;   \n}\n\n\n.container {\n    width: $container-width-md;\n    margin: 0 auto;\n}\n\n\n\n.clearfix {\n    @include clearfix;\n} \n\n.content-none {\n    @include content-none;\n}\n\n.text-none {\n    @include text-none;\n}\n\n.icon {\n    stroke-width: 0;\n    stroke: currentColor;\n    fill: currentColor;\n    overflow: hidden;\n    width: 1em;\n    height: 1em;\n    vertical-align: -0.15em;\n    fill: currentColor;\n    display: inline-block;\n    color: inherit;\n}\n\n\n@include media-lt-md {\n    .container {\n        width: auto;\n    }\n}\n\n@include media-lt-sm {\n    body, html, #app, #wrapper {\n        font-size: ($body-font-size * 0.875);\n    }\n}\n\n@include media-lt-xxs {\n    body, html, #app, #wrapper {\n        font-size: ($body-font-size * 0.8125);\n    }\n}\n\n@include media-gt-lg {\n    .container {\n        width: $container-width-lg;\n    }\n}\n\n\n\n#header {\n    font-size: 1.2em;\n    padding: .25em 0;\n    & .container {\n        display: flex;\n    }\n}\n\n\n#logo {\n    & .logo-separator, \n    & .logo-description {\n        @include content-none\n    }\n    & .logo-text{\n        vertical-align: middle;\n        display: block;\n        line-height: 1;\n    }\n    & .logo-name {\n        display: block;\n        line-height: 1;\n    }\n    & .logo-image {\n        vertical-align: middle;\n        height: 1em;\n        width: 1em;\n        display: block;\n    }\n    & a {\n        font-weight: normal;\n        width: 100%;\n        height: 100%;\n        line-height: 1;\n        display: flex;\n        &:hover {\n            text-decoration: none;\n        }\n        text-transform: uppercase;\n    }\n    font-size: 3em;\n    margin: 0;\n    padding: 0;\n    height: 1em;\n    width: auto;\n    white-space: nowrap;\n    flex-basis: 2%;\n}\n\n#header-nav-toggle{\n    flex-basis: 48%;\n    flex-grow: 1;\n    height: 3em;\n    padding: .3em 0;\n    & button {\n        display: none;\n        background: #eee;\n        border-radius: .25em;\n        color: #444;\n        padding: 0;\n        margin: 0;\n        border: 0;\n        width: 2.4em;\n        height: 2.4em;\n        & span {\n            @include content-none\n        }\n        & .icon{\n            width: 80%;\n            height: 80%;\n        }\n    }\n}\n\n#header-nav{\n    flex-basis: 48%;\n    flex-grow: 1;\n    height: 3em;\n    padding: .3em 0;\n    & ul {\n        padding: 0;\n        margin: 0;\n        list-style: none;\n        float: right;\n        height: 2.4em;\n        & li {\n            height: 100%;\n            display: inline-block;\n        }\n        & a {\n            display: inline-block;\n            line-height: 2.4;\n            padding: 0 .8em;\n        }\n    }\n}\n\n\n#main {\n    min-height: 95vh;\n    min-height: calc(100vh - 7em - 4em);\n}\n\n#footer {\n    background: #444;\n    color: #bbb;\n    & a {\n        color: #fff;\n        &:hover {\n            color: #bbb;\n        }\n    }\n}\n#footer-nav {\n    & ul {\n        margin: 0;\n        padding: 0;\n        display: flex;\n        & li {\n            text-align: center;\n            list-style: none;\n            flex-grow: 1;\n            & a {\n                padding: .8em;\n                display: inline-block;\n            }\n        }\n    }\n    border-bottom: 1px solid rgb(200, 200, 200, .2);\n}\n#footer-copyright {\n    text-align: center;\n    padding: .1em 0;\n}\n\n@include media-lt-md {\n    #header {\n        margin-left: .25em;\n        margin-right: .25em;\n    }\n    #header-nav-toggle {\n        padding-left: .3em;\n    }\n}\n\n.popup-alert-message {\n    max-width: 20em;\n    z-index: 1030;\n    position: fixed;\n    top: 40%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    .alert-message {\n        text-align: center;\n        opacity: .9;\n        border: 0;\n        margin: 0;\n        .btn {\n            display: none;\n        }\n    }\n}\n","$body-color: #333;\n$body-background: #fafafa;\n$body-font-size: 14px;\n$body-line-height: 1.5;\n$body-font-family: system-ui,\n-apple-system,\n\"Segoe UI\",\nRoboto,\n\"Helvetica Neue\",\nArial,\n\"Noto Sans\",\