
import {SetupContext, computed} from "vue"
import FormButton from "@/components/forms/Button.vue"

export default {
  components: {
    FormButton,
  },
  name: "AlertMessage",
  props: {
    type: {
      type: String,
      default: 'default',
    },
    value: {},
    
    close: {
      type: Boolean,
      default: false
    },
    onClose: {
    },
  },
  setup(props: any, context: SetupContext) {
    const translate = function(message: any) {
      let value
      if (!message || typeof message !== 'object') {
        value = message
      } else if (message.message) {
        value = message.message
      } else if (message.error) {
        value = message.error
      } else {
        value = message
      }
      return value
    }

    const onClose2 = () => {
      context.emit("close")
    }

    const forValue = computed((): any => {
      let value = props.value
      if (!value) {
        return []
      }

      if (value?.graphQLErrors?.length) {
        return value.graphQLErrors
      }
      if (value?.networkError?.result?.errors?.length) {
        return value.networkError.result.errors
      }

      if (value?.errors?.length) {
        return value.errors
      }

      // array
      if (value instanceof Array) {
        let messages: any[] = []
        for (let i = 0; i < value.length; i++) {
          const message = value[i];
          if (!message || typeof message !== 'object') {
            messages.push({message})
          } else {
            messages.push(message)
          }
        }
        return messages
      }

    if (value !== 'object') {
        return [{message: value}]
      }
      return [value]
    })

    return {translate, forValue, onClose2}
  }
}
