$name, $value in $btns {\n    @if $name == \"default\" {\n      .btn {\n        @include btn-mixin-value($value);\n      }  \n    } @else {\n      .btn-#{$name} {\n        @include btn-mixin-value($value);\n      }\n    }\n  }\n}\n\n\n\n.btn {\n  border-radius: .2em;\n  text-decoration: none;\n  border: 1px solid transparent;\n  padding: .375em .75em;\n  position: relative;\n  outline: 0;\n  font-size: 1em;\n  line-height: 1.5;\n  cursor: pointer;\n  display: inline-block;\n  font-weight: 400;\n  text-align: center;\n  vertical-align: middle;\n  user-select: none;\n  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;\n  margin-top: .25em;\n  margin-bottom: .25em;\n  white-space: nowrap;\n  &:disabled,\n  &.disabled {\n    cursor: default;\n    opacity: .65;\n  }\n  &:hover,\n  &.hover,\n  &:active,\n  &.active {\n    text-decoration: none;\n  }\n  &.outline {\n    background: transparent;\n  }\n}\n@include btns-mixin();\n\n.btn-submitting {\n  position: absolute;\n  z-index: 1;\n  background: inherit;\n  text-align: center;\n  left: 0;\n  right: 0;\n  svg {\n    font-size:1.5em;\n    animation: btn-submitting 1s linear infinite;\n  }\n}\n\n@keyframes btn-submitting {\n  from {\n    transform: rotate(0deg);\n  }\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n.btn-block {\n  width: 100%;\n  display: block;\n}\n\n\n.btn-xs {\n  font-size: 0.75em;\n}\n.btn-sm {\n  font-size: 0.875em;\n}\n.btn-lg {\n  font-size: 1.25em;\n}\n.btn-xl {\n  font-size: 1.5em;\n}\n"],"sourceRoot":""}]);
