___CSS_LOADER_EXPORT___.push([module.id, ".alert-message{border:1px solid rgba(0,0,0,0);border-radius:.25em;padding:1em;margin-bottom:1em}.alert-message.hide,.alert-message .hide{display:none}.alert-message p{margin:0 0 .75em 0}.alert-message p:last-child{margin-bottom:0}.alert-message .btn-text{float:right;display:block;padding:0 .25em;background:rgba(0,0,0,0);border:0;margin:0}.alert-message{color:#000;background:#fff;box-shadow:#ccc}.alert-message .btn-text{color:#000}.alert-message .btn-text:focus{box-shadow:0 0 0 .2em rgba(0,0,0,.35)}.alert-message-danger{color:#fff;background:#f44336;box-shadow:#c3362b}.alert-message-danger .btn-text{color:#fff}.alert-message-danger .btn-text:focus{box-shadow:0 0 0 .2em rgba(255,255,255,.35)}.alert-message-error{color:#fff;background:#f44336;box-shadow:#c3362b}.alert-message-error .btn-text{color:#fff}.alert-message-error .btn-text:focus{box-shadow:0 0 0 .2em rgba(255,255,255,.35)}.alert-message-warning{color:#fff;background:#f59300;box-shadow:#c47600}.alert-message-warning .btn-text{color:#fff}.alert-message-warning .btn-text:focus{box-shadow:0 0 0 .2em rgba(255,255,255,.35)}.alert-message-success{color:#fff;background:#4caf50;box-shadow:#3d8c40}.alert-message-success .btn-text{color:#fff}.alert-message-success .btn-text:focus{box-shadow:0 0 0 .2em rgba(255,255,255,.35)}.alert-message-info{color:#fff;background:#00bcd4;box-shadow:#0096aa}.alert-message-info .btn-text{color:#fff}.alert-message-info .btn-text:focus{box-shadow:0 0 0 .2em rgba(255,255,255,.35)}", "",{"version":3,"sources":["webpack://./src/components/AlertMessage.vue"],"names":[],"mappings":"AA4BA,eACE,8BAAA,CACA,mBAAA,CACA,WAAA,CACA,iBAAA,CAEA,yCACE,YAAA,CAEF,iBACE,kBAAA,CACA,4BACE,eAAA,CAGJ,yBACE,WAAA,CACA,aAAA,CACA,eAAA,CACA,wBAAA,CACA,QAAA,CACA,QAAA,CAhCE,eAdJ,UAAA,CACA,eAAA,CACA,eAAA,CACA,yBACE,UAAA,CACA,+BACE,qCAAA,CAYA,sBAlBJ,UAAA,CACA,kBAAA,CACA,kBAAA,CACA,gCACE,UAAA,CACA,sCACE,2CAAA,CAYA,qBAlBJ,UAAA,CACA,kBAAA,CACA,kBAAA,CACA,+BACE,UAAA,CACA,qCACE,2CAAA,CAYA,uBAlBJ,UAAA,CACA,kBAAA,CACA,kBAAA,CACA,iCACE,UAAA,CACA,uCACE,2CAAA,CAYA,uBAlBJ,UAAA,CACA,kBAAA,CACA,kBAAA,CACA,iCACE,UAAA,CACA,uCACE,2CAAA,CAYA,oBAlBJ,UAAA,CACA,kBAAA,CACA,kBAAA,CACA,8BACE,UAAA,CACA,oCACE,2CAAA","sourcesContent":[" \n@import \"@/scss/variable.scss\";\n@mixin alert-message-type-mixin($value) {\n  color: map-get($value, color);\n  background: map-get($value, background);\n  box-shadow: shade-color(map-get($value, background), 20);\n  & .btn-text {\n    color: map-get($value, color);\n    &:focus{\n      box-shadow: 0 0 0 0.2em rgba(map-get($value, color), .35);\n    }\n  }\n}\n\n@mixin alert-messages-mixin() {  \n  @each $name, $value in $alert-messages {\n    @if $name == \"default\" {\n      .alert-message {\n        @include alert-message-type-mixin($value);\n      }  \n    } @else {\n      .alert-message-#{$name} {\n        @include alert-message-type-mixin($value);\n      }\n    }\n  }\n}\n\n.alert-message{\n  border: 1px solid transparent;\n  border-radius: .25em;\n  padding: 1em;\n  margin-bottom: 1em;\n\n  &.hide, .hide {\n    display: none;\n  }\n  & p {\n    margin: 0 0 .75em 0;\n    &:last-child{\n      margin-bottom: 0\n    }\n  }\n  & .btn-text {\n    float: right;\n    display: block;\n    padding: 0 .25em;\n    background: transparent;\n    border: 0;\n    margin: 0;\n  }\n}\n@include alert-messages-mixin();\n"],"sourceRoot":""}]);
